.main-content {
    position: relative;
    width: 100%;
    height: 100vh; /* Full viewport height */
    overflow: hidden; /* Prevent overflow */
  }
  
  .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    object-fit: cover;
    z-index: -1; /* Keeps the background image behind content */
  }
  
  .realestate-logo {
    position: absolute;
    top: 20px; /* Adjust to your desired distance from the top */
    left: 50%;
    transform: translateX(-50%); /* Centers the logo horizontally */
    height: 55px;
    width: 245px;
    z-index: 1; /* Ensures logo stays above the background */
  }
  
  .content-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    text-align: center;
    padding: 20px;
    position: relative;
    z-index: 1; /* Ensures text and images are above the background */
  }
  
  .text-section {
    max-width: 800px;
    margin-bottom: 30px; /* Space between text section and image */
  }
  
  .headline {
    font-size: 3rem;
    margin-bottom: 20px;
    color: rgb(0, 0, 0); /* Ensure the headline is visible */
  }
  
  .description {
    font-size: 1.25rem;
    margin-bottom: 30px;
    color: rgb(0, 0, 0); /* Ensure the description is visible */
  }
  
  .cta-button {
    padding: 15px 30px;
    background-color: #007BFF;
    color: white;
    text-decoration: none;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
    display: inline-block;
  }
  
  .cta-button:hover {
    background-color: #0056b3;
  }
  
  .image-section {
    margin-top: 20px; /* Space between text and image section */
  }
  
  .image {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
  }
  
  /* Mobile responsiveness */
  @media (max-width: 768px) {
    .realestate-logo {
      width: 200px;
      height: auto;
    }
  
    .headline {
      font-size: 2rem;
    }
  
    .description {
      font-size: 1rem;
    }
  
    .cta-button {
      padding: 12px 25px;
    }
  }
  